body {
  font-family: "Open Sans", sans-serif;
  line-height: 150%;
}
.button {
  background-color: #cc8b79 !important;
  border-color: #cc8b79 !important;
}
.button:hover {
  background-color: white !important;
  border-color: #cc8b79 !important;
  color: #cc8b79 !important;
}
.padding-80-80 {
  padding: 80px 0px;
}
.padding30 {
  padding: 30px;
}
.padding-50-50 {
  padding: 50px 0px;
}
.padding-top {
  padding-top: 100px;
}
.mb20 {
  margin-bottom: 20px;
}
.post__title:hover {
  text-decoration: none;
}
/*---------------------------------
    FORMS & INPUT
---------------------------------*/

button:hover,
input[type="button"]:hover,
input[type="reset"]:hover,
input[type="submit"]:hover {
  /*border-color: #ccc #bbb #aaa;*/
  outline: 0;
}

button:active,
button:focus,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:active,
input[type="submit"]:focus {
  /*border-color: #aaa #bbb #bbb;*/
  outline: 0;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
  /*color: #666;
    border: 1px solid #ccc;
    border-radius: 3px;
    padding: 3px;*/
  outline: 0;
  width: 100%;
}

.bg-theme {
  background-color: #f8faff;
}
.app-text {
  color: #cc8b79;
}

.justify-between {
  justify-content: space-between;
}

.social-media {
  cursor: pointer;
}

.admission-bg {
  background-color: #f8faff;
}

/*---------------------------------
  Navbar css
---------------------------------*/

.navbar {
  border-bottom: 0;
  border-top: 0;
  margin-bottom: 0;
}
.navbar-brand {
  position: relative;
  padding: 15px 15px;
  color: #cc8b79;
  font-size: 18px;
  line-height: 20px;
}
.navbar-brand a,
a:hover {
  color: #cc8b79;
  text-decoration: none;
}
.navbar-brand a:hover {
  color: white;
}
.navlink {
  font-size: 15px;
  letter-spacing: 1px;
  color: white;
  font-family: "Open Sans", sans-serif;
  padding: 15px 15px;
  text-transform: capitalize;
  cursor: pointer;
}
.navlink a {
  color: white;
}

.navlink-active a {
  color: #cc8b79;
}

.navlink a:hover,
a:active {
  color: #faae7b;
  text-decoration: none;
}
/*---------------------------------
  Banner css
---------------------------------*/
p.banner-text-header {
  font-size: 15px;
  font-weight: 300;
  font-family: "Open Sans", sans-serif;
  width: 78ch;
}

h2.banner-header {
  font-weight: 200;
  font-family: "Open Sans", sans-serif;
  font-size: 60px;
}

/*---------------------------------
  Banner card css
---------------------------------*/
h3.title-box {
  font-weight: 200;
  font-family: "Open Sans", sans-serif;
  margin-bottom: 20px;
  color: #cc8b79;
}

p.body-box {
  font-size: 15px;
  font-weight: 300;
  font-family: "Open Sans", sans-serif;
}

h3 {
  font-size: 20px;
}

.top-header {
  height: 70vh;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.header-top-area {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 999;
}

.banner {
  height: 80vh;
  width: 100vw;
  background-attachment: fixed;
  background-position: 0 30%;
  background-repeat: no-repeat;
  background-size: cover;
}
.banner::before {
  background: #000 none repeat scroll 0 0;
  content: "";
  height: 100%;
  left: 0;
  opacity: 0.7;
  position: absolute;
  top: 0;
  width: 100%;
}
.caption-box {
  height: 100%;
}
.promo {
  background: #ffffff none repeat scroll 0 0;
  -webkit-box-shadow: 0 9px 9px 0 #c7c7c7;
  box-shadow: 0 9px 9px 0 #c7c7c7;
  margin-top: -100px;
  padding-top: 30px;
  position: relative;
  z-index: 1;
}
.text-box {
  position: relative;
  transition: all 0.3s ease 0s;
  z-index: 1;
  overflow: hidden;
  animation-name: fadeInUp;
  animation-duration: 0.1s;
}

.icon-box {
  background: #cc8b79 none repeat scroll 0 0;
  border-radius: 5px;
  color: #faae7b;
  height: 70px;
  margin-bottom: 40px;
  padding-top: 15px;
  text-align: center;
  width: 70px;
  font-size: 36px;
  line-height: 1;
}

.section-padding {
  padding: 100px 0;
}

a.enroll-button {
  border-radius: 50px;
  color: white;
  padding: 10px 15px;
  text-transform: capitalize;
}

a.enroll-button:hover {
  text-decoration: none;
}

/*--------------------------------
   4. BLOG
--------------------------------*/

.single-course {
  border-radius: 10px;
  -webkit-box-shadow: 0 7px 7px #c7c7c7;
  box-shadow: 0 7px 7px #c7c7c7;
  overflow: hidden;
  height: 100%;
}

.row.course-list {
  margin: 0;
}

.row.course-list .col-lg-3 {
  width: 100%;
  padding: 0;
}

.course-details {
  text-align: left;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.course-price {
  color: rgb(233, 169, 91);
  float: right;
  font-weight: 700;
}

.row.course-list .col-lg-3 {
  width: 100%;
  padding: 0;
}

h2.blog-header {
  color: #cc8b79;
}

/*--------------------------------
   4. Admission button
--------------------------------*/

.enroll-button a {
  border-radius: 50px;
  color: #ffffff;
  display: inline-block;
  font-weight: 600;
  height: 50px;
  padding-top: 13px;
  text-align: center;
  width: 150px;
}

.enroll-button a:hover {
  background: #171932 none repeat scroll 0 0;
}
h3.admission {
  color: #cc8b79;
}

/*--------------------------------
    5. VIDEO AREA
---------------------------------*/

.video-area .area-bg::before {
  background: bisque none repeat scroll 0 0;
  opacity: 0.75;
}

.video-promo-details {
  border-radius: 10px;
}

.video-promo-content {
  color: #ffffff;
  left: 0;
  position: relative;
  text-align: center;
  top: 50%;
  width: 100%;
}

.video-area-popup {
  background: #ffffff none repeat scroll 0 0;
  border-radius: 50%;
  color: #faae7b;
  cursor: pointer;
  display: inline-block;
  font-size: 30px;
  height: 70px;
  line-height: 1;
  padding-left: 5px;
  padding-top: 16px;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  width: 70px;
  top: 40%;
  position: absolute;
  bottom: 50%;
  right: 50%;
  left: 45%;
}

.video-area-popup:hover {
  background: #cc8b79 none repeat scroll 0 0;
  -webkit-box-shadow: 0 0 10px 10px #ffffff;
  box-shadow: 0 0 10px 10px #ffffff;
  color: #ffffff;
}

/*----------------------------------
    FOOTER LAYOUT: 768px
------------------------------------*/

.footer {
  background-color: #171932;
}
.footer a {
  color: white;
}
.footer li {
  margin-bottom: 12px;
}
a.footer:hover {
  color: #faae7b;
}

.footer-list ul li a:hover {
  color: #31dc89;
}

.subscriber-form {
  background: #ffffff none repeat scroll 0 0;
  border: 1px solid #dddddd;
  border-radius: 50px;
  font-size: 18px;
  height: 50px;
  position: relative;
  text-transform: capitalize;
}

.subscriber-form input {
  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: 0 none;
  border-radius: 5px;
  color: #000000;
  height: 100%;
  left: 0;
  letter-spacing: 1px;
  padding: 10px 10px 10px 30px;
  position: absolute;
  top: 0;
  width: 80%;
}

.subscriber-form button {
  background: #d3ffea none repeat scroll 0 0;
  border: 0 none;
  border-radius: 0 50px 50px 0;
  color: #171932;
  font-size: 20px;
  height: 100%;
  letter-spacing: 2px;
  position: absolute;
  right: 0;
  text-transform: uppercase;
  top: 0;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
  width: 80px;
}

.subscriber-form button:hover {
  background: #31dc89 none repeat scroll 0 0;
  color: #ffffff;
}

label.mt10 {
  bottom: -52px;
  left: 0;
  position: absolute;
  right: 0;
  text-align: center;
}

label.mt10.valid {
  bottom: 8px;
  color: #31dc89;
  left: 0;
  position: absolute;
  right: 0;
  text-align: center;
}

.footer-bottom-area {
  background: #2e2e43 none repeat scroll 0 0;
}

.footer-copyright {
  padding: 30px 0;
}

/*----------------------------------
    LAPTOP LAYOUT: 768px
------------------------------------*/

/*----------------------------------
    TABLET LAYOUT: 768px
------------------------------------*/

@media (min-width: 768px) and (max-width: 991px) {
  .caption-box h1 {
  }
}

/*----------------------------------
    MOBILE LAYOUT: 320px
-----------------------------------*/

@media only screen and (max-width: 767px) {
  .navlink {
    padding: 10px 15px;
    background-color: black;
  }
  .caption-box {
    width: 100%;
  }
  .caption-sub {
    width: 100%;
  }
  .caption-sub p {
    font-size: 16px;
  }
  .banner {
    height: 70vh;
    width: 100vw;
    background-attachment: unset;
    background-position: 0 50%;
    background-repeat: no-repeat;
    background-size: cover;
  }
  h2.banner-header {
    font-size: 40px;
  }
  p.banner-text-header {
    width: 45ch;
  }
  .card-margin-right-bottom {
    margin-right: 0;
    margin-bottom: 1rem;
  }
  .footer-container {
    margin-bottom: 3rem;
  }
}

